import { Config, HardwiredSourceObject } from "../types";

export const constructSource = (
  sourceCode: HardwiredSourceObject,
  hash: Number,
  dependencyIds: number[],
  config: Config
): string => {
  return `${config.defineModuleName}(${
    sourceCode.code
  },${hash},${JSON.stringify(dependencyIds)})`;
};
